<template>
    <el-dialog title="Add Period" :visible.sync="dialogForm" class="updatePwd">
        <el-form :model="form">
            <el-form-item label="PeriodType:" :label-width="formLabelWidth">
                <el-select
                        size="small"
                        v-model="form.type"
                        placeholder="Please choose"
                >
                    <el-option
                            v-for="(item ,$key) in type"
                            :label="item.name"
                            :value="item.value"
                            :key="$key">
                    </el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="periodCode:" :label-width="formLabelWidth">
                <el-input v-model="form.periodCode" auto-complete="off"></el-input>
            </el-form-item>
            <el-form-item label="companyCode:" :label-width="formLabelWidth">
                <el-input v-model="form.companyCode" auto-complete="off"></el-input>
            </el-form-item>
            <el-form-item label="startDate:" :label-width="formLabelWidth">
                <el-date-picker
                        size="small"
                        v-model="singleDate"
                        type="date"
                        placeholder="select startDate">
                </el-date-picker>
            </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
            <el-button @click="dialogForm = false">Cancel</el-button>
            <el-button type="primary" @click="submit()">Submit</el-button>
        </div>
    </el-dialog>
</template>

<style scope="" lang="less" type="text/scss">
.updatePwd{
    .el-dialog__header{
        padding:30px;
        .el-dialog__headerbtn{
            width:50px;
            height:50px;
            margin:-30px -30px 0 0;
        }
    }
    .el-dialog__body{
        padding:30px 50px 0 0;
    }
}
</style>

<script>
import dateFormat from '../../../src/module/DateFormat';
import QuRequest from '../../core/QuRequest'

const quRequest = new QuRequest()
export default {

    data() {
        return {
            type: [
                { 'name': 'Daily', 'value': '0'},
                { 'name': 'Weekly','value': '1'},
                { 'name': 'Monthly', 'value': '2'},
                { 'name': 'Annual', 'value': '3'}
            ],
            dialogForm: false,
            formLabelWidth: '200px',
            form:{
                type: "0",
                periodCode:'',
                companyCode:'',
                startDate:'',
            },
        }
    },
    computed: {

        singleDate: {
            set (newValue) {
                this.form.startDate = this.formatDate(newValue)
            },
            get () {
                return this.form.startDate
            }
        }

    },
    mounted() {

    },
    methods: {
        formatDate(dateStr){
            let date = new Date(dateStr);
            let y = date.getFullYear();
            let m = date.getMonth() + 1;
            let d = date.getDate();
            return [y, this.addZero(m), this.addZero(d)].join('/');
        },
        addZero: function (num) {
            return (num > 9 ? '' : '0') + num
        },
        showDialog() {
            this.dialogForm = true;
        },
        showInfoDialog() {
            this.form.type= '';
            this.form.periodCode= '';
            this.form.companyCode = '';
            this.form.startDate = '';
            this.dialogForm = true;
        },
        submit(){
            this.$updateRouter(this.form);
            let param = Object.assign({}, this.form);
            quRequest.send(quRequest.Apis.qutuiba.cdn.newperiod,param).then(res => {
                this.$message.success("Submit Success！");
                this.$emit('update')
            }).catch(err => {
                this.$message.error(`${ err.message || 'Unknown error' }`);
            }).fin(() => {
                this.loading = false;
            }).done();
            this.dialogForm = false;
        }
    }
}
</script>


